@font-face {
  font-family: 'Inter';
  src: url('./components/assets/fonts/static/Inter-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Inter-bold';
  src: url('./components/assets/fonts/static/Inter-Bold.ttf') format('truetype');
  font-weight: bold;
}

@font-face {
  font-family: 'Inter-bolder';
  src: url('./components/assets/fonts/static/Inter-ExtraBold.ttf') format('truetype');
  font-weight: bolder;
}




* {
  margin: 0;
  padding: 0;
}

/* 
  ##Device = Desktops
  ##Screen = 1281px to higher resolution desktops
*/

@media (min-width: 1281px) {
.img1 {
  width: 64.09px;
  height: 39.9px;
  margin-right: 614.19px;
}

.module1 {
  display: flex;
  margin-top: 89px;
  margin-left: 165px;
}

ul {
  display: flex;
  list-style-type: none;
  margin-left: 24px;
}

li {
  margin-bottom: 24px;
  margin-right: 40px;
}

a:hover {
  color: #F15D51;
}

a {
  color: #5E607A;
  text-decoration: none;
  font-size: 15px;
}

.mainSection {
  margin-right: 30px;
}

.mainImage {
  width: 730px;
  height: 300px;
  margin-bottom: 30px;
}

.MiddleSection {
  display: flex;
  margin-top: 55.1px;
  margin-left: 165px;
}

.mainHeader {
  display: flex;
}

.headerTitleBox {
  height: 182px;
  width: 350px;
  font-family: Inter-bold;
  font-size: 26px;
  font-weight: 800;
  line-height: 56px;
  letter-spacing: 0px;
  text-align: left;
  margin-right: 30px;
}

.MainHeaderInfo {
  height: 182px;
  width: 350px;
  font-family: Inter;
  font-size: 15px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 0px;

}

.btn1 {
  background-color: #F15D51;
  width: 185px;
  height: 48px;
  margin-top: 24px;
  display: grid;
  place-items: center;
  color: white;
  cursor: pointer;
}

.btn1:hover {
  background-color: #00001A;
  
}

.newSection {
  width: 343px;
  height: 511px;
  background-color: #00001A;
  margin-bottom: 64px
}

.newTitle {
  margin-left: 24px;
  padding-top: 32px;
  margin-bottom: 34px;
  color: #E9AA52;
  
}



.box {
  margin-left: 21px;
  height: fit-content;
}

.box h1 {
  font-family: Inter;
  font-size: 20px;
  font-weight: 800;
  color: #FFFDFA;
  margin-bottom: 8px;
  margin-top: 31px;
}

.box h1:hover {
  color: #E9AA52;
  cursor: pointer;
}

.box p {
  font-family: Inter;
  font-size: 15px;
  font-weight: 100;
  text-align: left;
  color: #C5C6CE;
  border-bottom: 1px solid white;
  padding-bottom: 29px;
}
.special-style p {
  border-bottom: #00001A;
}

.blogSection {
  display: flex;
  margin-bottom: 80px;
  margin-left: 165px;
}

.blogBox {
  display: flex;
  margin-left: 16px;
  margin-bottom: 32px;
  width: 350px;
}

.blogSection {
  margin-bottom: 80px;
}

.blogBox {
  display: flex;
  margin-left: 16px;
  margin-bottom: 32px;
}

.imgBlog {
  width: 100px;
  height: 130px;
  margin-right: 24px;
}

.blogInfo {
  height: 128px;
}

.blogInfo h1 {
  font-family: Inter-bold;
  font-size: 32px;
  font-weight: 700;
  color: #C5C6CE;
  margin-bottom: 12px;
}

.blogInfo h2 {
  color: #00001A;
  margin-bottom: 9px;
  font-family: Inter-bold;
  font-size: 18px;
  font-weight: 800;
  cursor: pointer;
}

.blogInfo h2:hover {
  color: #F15D51;
}

.blogInfo p {
  color: #5E607A;
}



  
  
}

